.search {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 20px;
}
  
.search__input {
    font-family: inherit;
    font-size: inherit;
    background-color: #f4f2f2;
    border: none;
    color: #646464;
    padding: 0.7rem 1rem;
    border-radius: 30px;
    width: min(100%, 600px);
    transition: all ease-in-out .5s;
    margin-right: -2rem;
    margin-left: -1rem;
}

.search__input:hover, .search__input:focus {
    box-shadow: 0 0 1em #00000013;
}
  
.search__input:focus {
    outline: none;
    background-color: #f0eeee;
}
  
.search__input::-webkit-input-placeholder {
    font-weight: 100;
    color: #ccc;
}
  
.search__input:focus + .search__button {
    background-color: #f0eeee;
}
  
.search__button {
    border: none;
    background-color: #f4f2f2;
    margin-top: .1em;
}
  
.search__button:hover {
    cursor: pointer;
}
  
.search__icon {
    height: 1.3em;
    width: 1.3em;
    fill: #b4b4b4;
}